import { useState, useEffect } from 'react';

export default function Navbar(){
    const [isScrolled, setIsScrolled] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 0) {
                setIsScrolled(true);
            } else {
                setIsScrolled(false);
            }
        };

        window.addEventListener('scroll', handleScroll);
        
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return(
        <div className={`fixed top-0 left-0 right-0 z-50 px-48 py-12 transition-all duration-300 ${
            isScrolled ? 'bg-white shadow-lg' : 'bg-transparent'
        }`}>
            <div className="flex justify-between items-center">
                <h1 className="text-[28px] font" style={{fontFamily: 'Sarina, cursive'}}>Nicxs</h1>

                <ul className="flex justify-between items-center gap-[80px] uppercase text-[16px]">
                    <li><a href="#" className="hover:text-[#286F6E] transition-colors">Services</a></li>
                    <li><a href="#" className="hover:text-[#286F6E] transition-colors">Works</a></li>
                    <li><a href="#" className="hover:text-[#286F6E] transition-colors">Notes</a></li>
                    <li><a href="#" className="hover:text-[#286F6E] transition-colors">Experience</a></li>
                </ul>
                <p className="text-[16px]">+63 9929770266</p>
            </div>
        </div>
    )
}