import Image from "next/image"
import Logo from "../../assets/crmc.png"
import Nico from "../../assets/nico.png"

export default function Hero(){
    return(
        <>
        <div className="w-full min-h-[100vh] relative">
            {/* Left */}
            <div className="relative lg:absolute lg:top-[11rem] flex flex-col justify-between min-h-[50vh] lg:h-[70vh] p-4 lg:p-0">
                <h1 className="text-[40px] sm:text-[50px] md:text-[60px] lg:text-[80px] font-semibold w-full lg:w-[35rem] leading-[1.2] lg:leading-[90px]">Here There, I'm Nico</h1>
                <h3 className="text-[#286F6E] font-semibold mt-4 lg:mt-0">nicxsician@gmail.com</h3>
                <div className="flex items-center gap-2 mt-4 lg:mt-0">
                    <p className="font-semibold text-[40px] sm:text-[50px] lg:text-[70px]">3</p>
                    <p className="text-[18px] lg:text-[23px] w-10 leading-[22px] lg:leading-[25px]">Work Experience</p>
                </div>
            </div>

            {/* Right */}
            <div className="relative lg:absolute lg:top-[13rem] flex flex-col justify-between min-h-[40vh] lg:h-[65vh] lg:right-0 w-full lg:w-[40rem] mt-8 lg:mt-0 p-4 lg:p-0">
                <p className="text-[16px] lg:text-[18px]">I do code to contribute 
                to positive change in society</p>
                <div className="w-full lg:w-[30vw] flex justify-center items-center flex-col mt-8 lg:mt-0">
                    <Image src={Logo} alt="Logo" className="w-[80%] lg:w-auto" />
                    <p className="w-full lg:w-[30%] text-center mt-3">Graduated Magna Cum Laude - Top 3  Overall</p>
                </div>
            </div>

            {/* Nico Image Center Bottom */}
            <div className="hidden lg:block absolute left-[55%] bottom-0 transform -translate-x-1/2 mb-4">
                <Image src={Nico} alt="Nico" className="w-auto h-auto" />
            </div>

            {/* Mobile Nico Image */}
            <div className="lg:hidden relative w-full flex justify-center mt-8">
                <Image src={Nico} alt="Nico" className="w-[80%] h-auto" />
            </div>
        </div>
        </>
    )
}